import React, { useState } from 'react';
import { ChevronDown, ChevronUp, Check } from 'lucide-react';

export default function Specification({ data }) {
  const specField = data?.find(f => f.name === "specifications");
  const features = specField?.items || [];
  const [isOpen, setIsOpen] = useState(false);

  if (features.length === 0) return null;

  return (
    <section className="py-6 px-4 -mx-4 bg-gray-50 sm:mx-0 sm:rounded-2xl">
      <div className="max-w-4xl mx-auto">
        
        {/* Centered clickable header */}
        <button
          onClick={() => setIsOpen(!isOpen)}
          className="w-full flex flex-col items-center justify-center gap-2 py-4 group tap-highlight-transparent"
        >
          <h2 className="text-xl font-bold text-gray-900">
            Specifications
          </h2>
          <div className="flex items-center gap-2 text-sm font-medium text-emerald-600">
            <span>{features.length} items</span>
            <span className="flex items-center gap-1">
              {isOpen ? 'Hide' : 'Show all'}
              {isOpen ? (
                <ChevronUp className="w-4 h-4 transition-transform group-active:scale-90" />
              ) : (
                <ChevronDown className="w-4 h-4 transition-transform group-active:scale-90" />
              )}
            </span>
          </div>
        </button>

        {/* Expanded content - smooth slide */}
        <div
          className={`overflow-hidden transition-all duration-500 ease-in-out ${
            isOpen ? 'max-h-96 opacity-100' : 'max-h-0 opacity-0'
          }`}
        >
          <div className="pt-4 pb-2 space-y-4">
            {features.map((item, index) => (
              <div key={index} className="flex gap-3">
                <Check className="w-5 h-5 text-emerald-600 flex-shrink-0 mt-0.5" />
                <div className="flex-1">
                  <p className="font-medium text-gray-900">{item.label}</p>
                  <p className="text-gray-600 text-sm mt-0.5">{item.value}</p>
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Optional: tiny fade gradient at bottom when closed (super premium touch) */}
        {!isOpen && features.length > 5 && (
          <div className="h-8 bg-gradient-to-t from-gray-50 to-transparent -mt-8 pointer-events-none" />
        )}
      </div>
    </section>
  );
}